* {
  box-sizing: border-box;
}

html {
  text-size-adjust: 100%;
}

body {
  margin: 0;
  padding: 1rem;
  background-color: #000;
  color: #fff;
  font-family: Futura, 'Trebuchet MS', Arial, sans-serif;
}

#header {
  margin: 2rem 0 1rem 0;
  text-align: center;
}

.markdown-previewer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  padding: 1rem;
}

textarea {
  max-width: 650px;
  height: 200px;
  margin-bottom: 1rem;
  border: 0;
  color: #fff;
}

textarea,
#preview {
  width: 100%;
  padding: .5rem;
  border-radius: .5rem;
  background-color: #333;
}

#preview {
  max-width: 850px;
  text-align: left;
}

code {
  display: inline-block;
  padding: .5rem;
  border-radius: .5rem;
  background-color: #000;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#footer {
  margin-top: 1rem;
  font-size: .75rem;
  color: #ccc;
  text-align: center;
}

#footer a {
  color: #ccc;
}
